/**
 * Vgrehajtja a Shift-or mintaillesztsi algoritmust, hogy megtallja a mintaegyezseket a szvegben.
 * @param {string} pattern - A keresend minta.
 * @param {string} text - A szveg amiben keresnk.
 * @returns {Array.<number>} - Indexek tmbje, ahol egyezsek tallhatk.
 */
function shiftOr(pattern, text) {
    let state = 0;
    let mask = [];
    let matches = [];
    let m = pattern.length;
    if (m === 0) {
        return matches
    }
    for (let i = 0; i < m; i++) {
        mask[pattern[i]] = mask[pattern[i]] | (1 << i);
    }
    for (let i = 0; i < text.length; i++) {
        state = (state << 1) + 1;
        state = state & mask[text[i]];
        if ((state & (1 << (m - 1))) !== 0) {
            matches.push(i - pattern.length + 1);
        }
    }
    return matches;
}

/**
 * Exportlja a Shift-or mintaillesztsi algoritmust ms modulokban val hasznlatra.
 * @module NotSoNaivePatternMatching
 */
module.exports = shiftOr;
