import React from 'react';

/**
 * Functional component lers megjelntse az alapjn milyen propertivel hvtuk meg.
 * @param {Object} props - Component properties.
 * @param {number} props.number - A szm ami reprezenztlja a keres algoritmust.
 * @returns {JSX.Element} - React JSX element.
 */
const Description = ({ number }) => {
    if (number === 0) {
        return <div>A naiv algoritmus <p>Minden lehetsges helyen megprblja illeszteni a keresett stringet a szvegre.</p></div>;
    }
    if (number === 1) {
        return <div>A Not So Naive algoritmus.
            <p>A naiv algoritmus azzal kiegsztve, hogy a mintaillesztst a msodik karaktertl kezdve kezdi vizsglni.</p>
        </div>;
    }
    if (number === 2) {
        return <div>A Boyer-Moore algoritmus.
            <p>A hatkony ugrs rdekbe kt szably alapjn ugrik elre a szvegben. Szveget balrl jobbra, mintt jobbrl balra olvassa.</p>
            <p>1. Bad character szably</p>
            <p>2.Good Suffix szably</p>
        </div>;
    }
    if (number === 10) {
        return <div>Naiv algoritmus Hamming tvolsggal kiegsztve.
            <p>Brute force-al keresi a mintt de Hamming tvolsgnyi eltrst enged.</p>
        </div>
    }
    if (number === 4) {
        return <div>Knuth-Morris-Pratt algoritmus.
            <p>Az algoritmus a minta elzetes vizsglatval elre meghatrozza, hogy a minta adott karaktere nem illeszkedik a szvegre, akkor mekkora az a legkisebb ugrs amg biztosan nem lesz egyezs.</p>
        </div>
    }
    if (number === 6) {
        return <div>Aho-Corasick algoritmus.
            <p>Az algoritmus egy automatt pt a mintkbl, s ezzel egyszerre tbb mintt is lehet keresni egy szvegben gyorsan s hatkonyan.</p>
        </div>
    }
    if (number === 8) {
        return <div>Karp and Rabin algoritmus.
            <p>Az algoritmus a minta s a szveg sszehasonltshoz egy hashfggvnyt hasznl, gy csak a hashrtkek egyenlsgnek ellenrzsre van szksg, ami gyorsabb, mint a karakterek egyezsgnek ellenrzse.</p>
        </div>
    }
    if (number === 7) {
        return <div>Shift-Or algoritmus.
            <p>Az algoritmus egy hatkony mintaillesztsi algoritmus, amely egy biteltolst hasznl.</p>
        </div>
    }

    return null;
};

export default Description;