/**
 * Elfeldolgozsa Karp-Rabin algoritmusnak.
 * @param {string} pattern - A minta string.
 * @param {string} text - A szveg string.
 * @returns {Array.<number>} - A tmb ami tartalmazza a pre-processing vgeredmnyt.
 */
function preKR(pattern, text) {
    let d, hx, hy, i;
    let m = pattern.length;
    let n = text.length;

    for (d = i = 1; i < m; ++i) {
        d = (d << 1);
    }

    for (hy = hx = i = 0; i < m; ++i) {
        hx = ((hx << 1) + pattern.charCodeAt(i));
        hy = ((hy << 1) + text.charCodeAt(i));
    }

    return [d, m, n, hx, hy]
}

/**
 * Vgrehajtja a Karp-Rabin algoritmust hogy megtallja a minta elfordulst a szvegbe
 * @param {string} pattern - A minta string amit keresnk
 * @param {string} text - A szveg string amin keresnk.
 * @param {Array.<number>} pre - Az elfeldolgozs a preKR fggvny eredmnye.
 * @returns {Array.<number>} - Indexek tmbje, ahol egyezsek tallhatk.
 */
function KR(pattern, text, pre) {
    let hx = pre[3];
    let hy = pre[4];
    let d = pre[0];
    let m = pre[1];
    let n = pre[2];
    let output = []

    let j = 0;
    while (j <= n - m) {
        if (hx === hy && pattern === text.substring(j, j + m)) {
            output.push(j)
        }

        hy = ((hy - text.charCodeAt(j) * d) << 1) + text.charCodeAt(j + m);
        ++j;
    }
    return output
}

/**
 * Exportlja a Karp-Rabin algoritmust s az elfeldolgozsi fggvnyt ms modulokban val hasznlatra.
 * @module KarpRabin
 */
module.exports = {
    KR: KR,
    preKR: preKR
};
