/**
 * Vgrehajtja a naiv mintaillesztsi algoritmust, hogy megtallja a mintaegyezseket a szvegben.
 * @param {string} pat - A keresend minta.
 * @param {string} txt - A szveg amiben keresnk.
 * @returns {Array.<number>} - Indexek tmbje, ahol egyezsek tallhatk.
 */
function naive(pat, txt) {
  let M = pat.length;
  let N = txt.length;
  const result = [];

  for (let i = 0; i <= (N - M); i++) {
    let j;
    for (j = 0; j < M; j++) {
      if (txt[i + j] !== pat[j]) {
        break;
      }
    }
    if (j === M) {
      result.push(i);
    }
  }

  return result;
}

/**
 * Vgrehajtja a naiv mintaillesztsi algoritmust egy megadott Hamming-tvolsggal.
 * @param {string} pat - A keresend minta.
 * @param {string} txt - A szveg amiben keresnk.
 * @param {number} hdistance - A megengedett legnagyobb Hamming-tvolsg.
 * @returns {Array.<number>} - Indexek tmbje, ahol egyezsek tallhatk.
 */
function naiveH(pat, txt, hdistance) {
  let M = pat.length;
  let N = txt.length;
  const result = [];

  for (let i = 0; i <= (N - M); i++) {
    let j;
    let nmm = 0;
    for (j = 0; j < M; j++) {
      if (txt[i + j] !== pat[j]) {
        nmm++;
        if (nmm > hdistance) {
          break;
        }
      }
    }
    if (nmm <= hdistance) {
      result.push(i);
    }
  }

  return result;
}

/**
 * Exportlja a naiv mintaillesztsi algoritmusokat ms modulokban val hasznlatra.
 * @module NaivePatternMatching
 */
module.exports = {
  naive: naive,
  naiveH: naiveH
};
