import React from 'react';
import DotComponent from './dot'

/**
 * Kiszmtja a szzalkot a megadott rtkek alapjn.
 * @param {number} e - A szmll rtke.
 * @param {number} full - A nevez rtke.
 * @returns {number} - A kiszmolt szzalk kerektve.
 */
function szazalek(e, full) {
  const percentage = (e / full) * 100;
  return  Math.round(Math.round(percentage * 100) / 100);
}

/**
 * A kimeneti terletet reprezentl funkcionlis komponens.
 * @param {Object} props - Component properties.
 * @param {number} props.index - A mintaegyezs indexe.
 * @param {number} props.element - Az elem helyzete.
 * @param {number} props.long - A szveg hossza.
 * @param {string} props.pattern - A a minta.
 * @returns {JSX.Element} - React JSX element.
 */
const OutputArea = (props) => {
  return (
    <div>
      <h4>{props.index}. index tallat</h4>
      <p>Az {props.element}. karakteren kezddik. Szveg hossza: {props.long}</p>
      <DotComponent number={szazalek(props.element, props.long)} long={szazalek(props.element+props.pattern.length,props.long)}/>
    </div>
  );
};

export default OutputArea;
