import React from 'react';

/**
 * Functional component pont kontner megjelentshez kiemelt ponttal a megadott szm alapjn.
 * @param {Object} props - Component properties.
 * @param {number} props.number - A minta elejnek szzalka.
 * @param {number} props.long - A minta vge szzalkban.
 * @returns {JSX.Element} - React JSX element.
 */
const DotComponent = ({ number,long }) => {
  const dots = Array.from({ length: 101 }, (_, index) => (
    <span key={index} className="dot" style={{ backgroundColor: (index >= number && index <= long) ? '#5865F2' : '#424549' }}></span>
  ));
  return <div className="dot-container">{dots}</div>;
};

export default DotComponent;
