/**
 * Kiszmtja a string kt indexe kztti illeszked substring hosszt.
 * @param {string} S - A bemeneti string.
 * @param {number} idx1 - Az els kezd indexe a substring-nek.
 * @param {number} idx2 - A msodik kezd indexe a substring-nek.
 * @returns {number} - A hoszza az illeszked substring-nek.
 */
function match_length(S, idx1, idx2) {
    if (idx1 === idx2) {
        return S.length - idx1;
    }
    let match_count = 0;
    while (idx1 < S.length && idx2 < S.length && S[idx1] === S[idx2]) {
        match_count++;
        idx1++;
        idx2++;
    }
    return match_count;
}

/**
 * Elvgzi a Boyer-Moore algoritmus alapvet elfeldolgozst.
 * @param {string} S - A bemeneti string.
 * @returns {Array.<number>} - Az alapvet elfeldolgozsi eredmnyt reprezentl tmb.
 */
function fundamental_preprocess(S) {
    if (S.length === 0) {
        return [];
    }
    if (S.length === 1) {
        return [1];
    }

    const z = new Array(S.length).fill(0);
    z[0] = S.length;
    z[1] = match_length(S, 0, 1);

    for (let i = 2; i <= z[1]; i++) {
        z[i] = z[1] - i + 1;
    }

    let l = 0;
    let r = 0;

    for (let i = 2 + z[1]; i < S.length; i++) {
        if (i <= r) {
            const k = i - l;
            const b = z[k];
            const a = r - i + 1;

            if (b < a) {
                z[i] = b;
            } else {
                z[i] = a + match_length(S, a, r + 1);
                l = i;
                r = i + z[i] - 1;
            }
        } else {
            z[i] = match_length(S, 0, i);
            if (z[i] > 0) {
                l = i;
                r = i + z[i] - 1;
            }
        }
    }

    return z;
}

/**
 * A Hibs karaktervltsi tblzatt lltja el.
 * @param {string} pattern - A minta string.
 * @returns {Array.<Array.<number>>} - hibs karaktervltsi tblzat.
 */
function bad_character_table(pattern) {
    //const ALPHABET_SIZE = 0x10FFFF; //minden karakterrej csak lass az elfeldolgozs
    const ALPHABET_SIZE = 2047; //legtbb esetben elg
    if (pattern.length === 0) {
        return Array.from({ length: ALPHABET_SIZE }, () => []);
    }

    const bct = Array.from({ length: ALPHABET_SIZE }, () => [-1]);
    const alpha = Array(ALPHABET_SIZE).fill(-1);

    for (let i = 0; i < pattern.length; i++) {
        alpha[(pattern[i]).codePointAt(0)] = i;

        for (let j = 0; j < alpha.length; j++) {
            bct[j].push(alpha[j]);
        }
    }

    return bct;
}

/**
 * Ltrehozza a good suffix shift tblzatot.
 * @param {string} pattern - A minta string.
 * @returns {Array.<number>} - A good suffix shift tbla.
 */
function good_suffix_table(pattern) {
    const gst = Array(pattern.length).fill(-1);
    const fp = fundamental_preprocess([...pattern].reverse());

    fp.reverse();

    for (let j = 0; j < pattern.length - 1; j++) {
        const i = pattern.length - fp[j];
        if (i !== pattern.length) {
            gst[i] = j;
        }
    }

    return gst;
}

/**
 * Generlja a full shift tblt.
 * @param {string} pattern - A minta string.
 * @returns {Array.<number>} - A full shift tbla.
 */
function full_shift_table(pattern) {
    const fst = Array(pattern.length).fill(0);
    const fp = fundamental_preprocess(pattern);
    let longest = 0;

    for (let i = fp.length - 1; i >= 0; i--) {
        longest = Math.max(fp[i], longest);
        fst[i] = longest;
    }

    return fst;
}

/**
 * Boyer-Moore algoritmust al megkeresi a minta tallatait a szvegben.
 * @param {string} P - A minta amit keresnk.
 * @param {string} T - A szveg amibe keresnk.
 * @param {Array.<Array.<number>>} R - A bad character shift tbla.
 * @param {Array.<number>} L - Good suffix shift tbla.
 * @param {Array.<number>} F - Full shift tbla.
 * @returns {Array.<number>} - Tmb amibe a tallt helyek vannak.
 */
function boyerMoore(P, T, R, L, F) {
    if (P.length === 0 || T.length === 0 || T.length < P.length) {
        return [];
    }
    const matches = [];
    let k = P.length - 1;
    let previous_k = -1;

    while (k < T.length) {
        let i = P.length - 1;
        let h = k;

        while (i >= 0 && h > previous_k && P[i] === T[h]) {
            i--;
            h--;
        }

        if (i === -1 || h === previous_k) {
            matches.push(k - P.length + 1);
            k += P.length - (P.length > 1 ? F[1] : 1);
        } else {
            const char_shift = i - R[(T[h]).codePointAt(0)][i];

            let suffix_shift;
            if (i + 1 === P.length) {
                suffix_shift = 1;
            } else if (L[i + 1] === -1) {
                suffix_shift = P.length - F[i + 1];
            } else {
                suffix_shift = P.length - 1 - L[i + 1];
            }

            const shift = Math.max(char_shift, suffix_shift);
            previous_k = shift >= i + 1 ? k : previous_k;
            k += shift;
        }
    }

    return matches;
}

/**
 * Exportlja a Boyer-Moore algoritmust s a kapcsold tblzatokat ms modulokban val hasznlatra.
 * @module BoyerMoore
 */
module.exports = {
    boyerMoore: boyerMoore,
    full_shift_table: full_shift_table,
    bad_character_table: bad_character_table,
    good_suffix_table: good_suffix_table
};
