/**
 * Vgrehajtja a "Not so naiv" mintailleszt algoritmust, hogy megtallja a mintaegyezseket a szvegben.
 * @param {string} pattern - A keresend minta.
 * @param {string} text - A szveg amiben keresnk.
 * @returns {Array.<number>} - Indexek tmbje, ahol egyezsek tallhatk.
 */
function notSoNaive(pattern, text) {
    let j, k, ell;
    let m = pattern.length;
    let n = text.length;

    if (pattern[0] === pattern[1]) {
        k = 2;
        ell = 1;
    } else {
        k = 1;
        ell = 2;
    }

    j = 0;
    let output = [];
    while (j <= n - m) {
        if (pattern[1] !== text[j + 1]) {
            j += k;
        } else {
            if (
                pattern.slice(2) === text.slice(j + 2, j + m) &&
                pattern[0] === text[j]
            ) {
                output.push(j)
            }
            j += ell;
        }
    }
    return output;
}

/**
 * Exportlja a "Not So Naive" mintaillesztsi algoritmust ms modulokban val hasznlatra.
 * @module NotSoNaivePatternMatching
 */
module.exports = notSoNaive;



