/**
* Az Aho-Corasick algoritmus ltal hasznlt Trie csompontjt jelli.
* @class
*/
class TrieNode {
  /**
  * Inicializl egy j TrieNode-ot.
  * @constructor
  */
  constructor() {
    /**
     * Az aktulis csompont gyermekcsompontjait reprezentl sztr.
     * @type {Object.<string, TrieNode>}
     */
    this.children = {};

    /**
     * Egy tmb, amely ennl a csompontnl vgzd mintkat tartalmaz.
     * @type {Array.<string>}
     */
    this.output = [];

    /**
     * Hivatkozs a Trie hibacsompontjra.
     * @type {TrieNode|null}
     */
    this.failure = null;
  }
}

/**
* Az Aho-Corasick algoritmust.
* @class
*/
class AhoCorasick {

  /**
   * Inicializl egy j AhoCorasick-pldnyt egy res Trie-vel.
   * @constructor
   */
  constructor() {

    /**
     * Az Aho-Corasick algoritmusban hasznlt Trie gykrcsompontja.
     * @type {TrieNode}
     */
    this.root = new TrieNode();
  }

  /**
   * Mintt hozzadja a Trie-hez.
   * @param {string} pattern - Minta amit a Trie-hez ad.
   */
  addPattern(pattern) {
    let node = this.root;
    for (let char of pattern) {
      if (!node.children[char]) {
        node.children[char] = new TrieNode();
      }
      node = node.children[char];
    }
    node.output.push(pattern);
  }

  /**
   * A mintailleszts optimalizlsa rdekben ltrehozza a hibahivatkozsokat a Trie-ben.
   */
  buildFailureLinks() {
    const queue = [];

    for (const key in this.root.children) {
      const child = this.root.children[key];
      child.failure = this.root;
      queue.push(child);
    }

    while (queue.length > 0) {
      const currentNode = queue.shift();

      for (const key in currentNode.children) {
        const child = currentNode.children[key];
        queue.push(child);

        let failureNode = currentNode.failure;

        while (failureNode !== null && !failureNode.children[key]) {
          failureNode = failureNode.failure;
        }

        child.failure = failureNode ? failureNode.children[key] : this.root;

        child.output = child.output.concat(child.failure.output);
      }
    }
  }

  /**
   * Megkeresi az adott minttkat a szvegbe Aho-Corasick algoritmusal.
   * @param {string} text - A mintk keresshez szksges szveg.
   * @returns {Array.<{ pattern: string, index: number }>} - Egyez mintkat s indexeiket tartalmaz objektumok tmbje.
   */

  search(text) {
    let node = this.root;
    const results = [];

    for (let i = 0; i < text.length; i++) {
      const char = text[i];

      while (node !== this.root && !node.children[char]) {
        node = node.failure;
      }

      if (node.children[char]) {
        node = node.children[char];
      } else {
        node = this.root;
      }

      for (const pattern of node.output) {
        results.push({ pattern, index: i - pattern.length + 1 });
      }
    }

    return results;
  }
}

/**
 * Exportlja az AhoCorasick osztlyt ms modulokban val hasznlatra.
 * @module AhoCorasick
 */
module.exports = AhoCorasick;
